export default ()=> {
      return (
        <div className="question" data-index="91" dangerouslySetInnerHTML = {{ __html: `
<h6>91. 输出什么?</h6>
<pre class="language-javascript"><code class="language-javascript"><span class="token keyword keyword-let">let</span> newList <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token number">1</span><span class="token punctuation">,</span> <span class="token number">2</span><span class="token punctuation">,</span> <span class="token number">3</span><span class="token punctuation">]</span><span class="token punctuation">.</span><span class="token function">push</span><span class="token punctuation">(</span><span class="token number">4</span><span class="token punctuation">)</span>

console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span>newList<span class="token punctuation">.</span><span class="token function">push</span><span class="token punctuation">(</span><span class="token number">5</span><span class="token punctuation">)</span><span class="token punctuation">)</span>
</code></pre>
<ul>
<li>A: <code>[1, 2, 3, 4, 5]</code></li>
<li>B: <code>[1, 2, 3, 5]</code></li>
<li>C: <code>[1, 2, 3, 4]</code></li>
<li>D: <code>Error</code></li>
</ul>
<details><summary><b>答案</b></summary>
<p>
<h4>答案: D</h4>
<p><code>.push</code>方法返回数组的长度，而不是数组本身！ 通过将<code>newList</code>设置为<code>[1,2,3].push(4)</code>，实际上<code>newList</code>等于数组的新长度：<code>4</code>。</p>
<p>然后，尝试在<code>newList</code>上使用<code>.push</code>方法。 由于<code>newList</code>是数值<code>4</code>，抛出TypeError。</p>
</p>
</details>
` }}></div>
      );
    }